import React from 'react';
import classNames from 'classnames';
import { SectionSplitProps } from '../../utils/SectionProps';
import Image from '../elements/Image';
import SmallSplit from '../sections/partials/SmallSplit';

const propTypes = {
  ...SectionSplitProps.types
}

const defaultProps = {
  ...SectionSplitProps.defaults
}

const NecoleBringsYou = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  invertMobile,
  invertDesktop,
  alignTop,
  imageFill,
  ...props
}) => {

  const outerClasses = classNames(
    'features-split section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'features-split-inner section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  const splitClasses = classNames(
    'split-wrap',
    invertMobile && 'invert-mobile',
    invertDesktop && 'invert-desktop',
    alignTop && 'align-top'
  );

  return (
    <section
      {...props}
      className={outerClasses}
      id="benefits"
    >
      <div className="container">
        <div className={innerClasses}>
          <h2 className="center-content"><span className="text-color-primary ">necole</span> <span className="m-0 fw-400 text-color-low">brings you...</span></h2>
          <div className={splitClasses}>

            {/* jokhoo rework this */}

            {/* MOTIVATION */}
            <div className="split-item screen-reader"></div>
            <div className="split-item">
              <div className="split-item-content center-content-mobile reveal-from-left" data-reveal-container=".split-item">
                <h2 className="text-color-primary fw-600 mb-8">
                  Motivation
                  </h2>
                <div>
                  <h5 className="mt-0 mb-12 fw-400 invert-color">
                    <span className="text-color-high">
                      Stay accountable with <span className="text-color-primary fw-600">necole</span> as your guide.
                        </span>
                  </h5>
                </div>
                <div className="reveal-from-left" data-reveal-delay="200">
                  <SmallSplit
                    title="Nudges"
                    text="Get notifications on learning opportunities up to 3 times a week."
                    imgsrc={require('./../../assets/images/bell-ringing.png')}
                    alt="bell-ringing"
                  />
                  <SmallSplit
                    title="Community"
                    text="Be inspired by fellow users & experts."
                    imgsrc={require('./../../assets/images/community.png')}
                    alt="community"
                  />
                  <div className="ta-l reveal-from-bottom center-content-mobile" data-reveal-delay="100">
                    <h4 className="invert-color p-0 mb-0 fw-200">The best of intentions don’t always lead to action.</h4>
                    <h4 className="invert-color p-0 m-0 fw-400">What keeps you going?</h4>
                  </div>
                </div>
              </div>
              <div className={
                classNames(
                  'split-item-image center-content-mobile reveal-from-bottom',
                  imageFill && 'split-item-image-fill'
                )}
                data-reveal-container=".split-item">
                <Image
                  src={require('./../../assets/images/phone-1.gif')}
                  alt="Phone-1"
                  width={960}
                  height={1080} />
              </div>
            </div>
            {/* END OF MOTIVATION */}

            {/* CONVENIENCE */}
            <div className="split-item screen-reader"></div>
            <div className="split-item">
              <div className="split-item-content center-content-mobile reveal-from-right" data-reveal-container=".split-item">
                <h2 className="text-color-primary fw-600 mb-8">
                  Convenience
                  </h2>
                <div>
                  <h5 className="mt-0 mb-12 fw-400 invert-color">
                    <span className="text-color-high">
                      Learn your way - anytime, anywhere.
                        </span>
                  </h5>
                </div>
                <div className="reveal-from-right " data-reveal-delay="200">
                  <SmallSplit
                    title="Bite-Sized Mobile Content"
                    text="Consume thousands of content via mobile in under 10 minutes."
                    imgsrc={require('./../../assets/images/bite-sized.png')}
                    alt="bite-sized"
                  />
                  <SmallSplit
                    title="Multi-Format Content"
                    text="Read, watch or listen to videos, articles, podcasts, webinars & interactive courses."
                    imgsrc={require('./../../assets/images/multi-format.png')}
                    alt="multi-format"
                  />
                  <div className="ta-l reveal-from-bottom center-content-mobile" data-reveal-delay="100">
                    <h4 className="invert-color p-0 mb-0 fw-200">Time is scarce.</h4>
                    <h4 className="invert-color p-0 m-0 fw-400">What will you choose to do with it?</h4>
                  </div>
                </div>

              </div>
              <div className={
                classNames(
                  'split-item-image center-content-mobile reveal-from-bottom',
                  imageFill && 'split-item-image-fill'
                )}
                data-reveal-container=".split-item">
                <Image
                  src={require('./../../assets/images/phone-2.gif')}
                  alt="Phone-2"
                  width={960}
                  height={1080} />
              </div>
            </div>
            {/* END OF CONVENIENCE */}

            {/* EXPERTISE */}
            <div className="split-item screen-reader"></div>
            <div className="split-item">
              <div className="split-item-content center-content-mobile reveal-from-left" data-reveal-container=".split-item">
                <h2 className="text-color-primary fw-600 mb-8">
                  Expertise
                  </h2>
                <div>
                  <h5 className="mt-0 mb-12 fw-400 invert-color">
                    <span className="text-color-high">
                      Learn your way - anytime, anywhere.
                        </span>
                  </h5>
                </div>
                <div className="reveal-from-left" data-reveal-delay="200">
                  <SmallSplit
                    title="Curation Process"
                    text="Consume thousands of content via mobile in under 10 minutes."
                    imgsrc={require('./../../assets/images/curation.png')}
                    alt="curation"
                  />
                  <SmallSplit
                    title="Exclusive Original Content"
                    text="Access our L52 pathways & webinars for a truly unique learning experience."
                    imgsrc={require('./../../assets/images/exclusive.png')}
                    alt="exclusive"
                  />
                  <div className="ta-l reveal-from-bottom center-content-mobile" data-reveal-delay="100">
                    <h4 className="invert-color p-0 mb-0 fw-200">Do you feel overwhelmed?</h4>
                    <h4 className="invert-color p-0 m-0 fw-400">Be guided by a leadership development authority.</h4>
                  </div>
                </div>
              </div>
              <div className={
                classNames(
                  'split-item-image center-content-mobile reveal-from-left',
                  imageFill && 'split-item-image-fill'
                )}
                data-reveal-container=".split-item">
                <Image
                  src={require('./../../assets/images/phone-3.gif')}
                  alt="Phone-3"
                  width={960}
                  height={1080} />
              </div>
            </div>
            {/* END OF CONVENIENCE */}

          </div>
        </div>
      </div>
    </section>
  );
}

NecoleBringsYou.propTypes = propTypes;
NecoleBringsYou.defaultProps = defaultProps;

export default NecoleBringsYou;