import React from 'react';
import classNames from 'classnames';
import Image from '../../elements/Image';

const ClientTestimonial = ({
  name,
  text,
  imgsrc,
  alt,
  className,
  classNameImg,
  ...props
}) => {

    const classes = classNames( 
        // 'client-testimonial',
        'm-4',
        className
      );

  return (
    <div
      {...props}
      className={classes}      
      >
        <div className="p-32 center-content">
          <h5 className="fw-400 invert-color">{text}</h5>
          <div className="p-4">
            <Image
                className={classNameImg}
                src={imgsrc}
                alt={alt}
                width={100}
                height={100} />
            </div>
            <h5 className="invert-color text-xs">{name}</h5>            
          </div>
        
      </div>
  );
}

export default ClientTestimonial;