import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { SectionProps } from '../../utils/SectionProps';
import Image from '../elements/Image';

const propTypes = {
  children: PropTypes.node,
  ...SectionProps.types
}

const defaultProps = {
  children: null,
  ...SectionProps.defaults
}

const Traits = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  invertMobile,
  invertDesktop,
  alignTop,
  imageFill,
  ...props
}) => {

  const outerClasses = classNames(
    'section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  const splitClasses = classNames(
    'split-wrap',
    invertMobile && 'invert-mobile',
    invertDesktop && 'invert-desktop',
    alignTop && 'align-top'
  );

  return (
    <section
      {...props}
      className={outerClasses}
    >
      <div className="m-32 center-content">
        <div className={innerClasses}>
        <div className={splitClasses}>
            <div className="split-item border-primary pl-32">
              <div className="split-item-content ta-l pl-32 ml-32 center-content-mobile reveal-from-left" style={{width:'60%'}} data-reveal-container=".split-item">
                <h2 className="text-color-primary fw-400">Looking to find out more about<br />your <strong>strengths</strong> instead?</h2>        
                <h4 className="text-color-mid fw-400">Complete a 15-minute assessment to get deep insights<br />into your performance potential.</h4>
              </div>
              <div className={
                classNames(
                  'split-item-image center-content-mobile reveal-from-bottom pr-32 mr-32',
                  imageFill && 'split-item-image-fill'
                )}         
                data-reveal-container=".split-item">
                <a href="/traits">
                  <Image
                    src={require('./../../assets/images/traits-logo.png')}
                    alt="traits-logo"
                     width={200}
                    // height={500} 
                    /> 
                  </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

Traits.propTypes = propTypes;
Traits.defaultProps = defaultProps;

export default Traits;