import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { SectionProps } from '../../utils/SectionProps';

const propTypes = {
  children: PropTypes.node,
  ...SectionProps.types
}

const defaultProps = {
  children: null,
  ...SectionProps.defaults
}

class AccordionItem extends React.Component {
  state = {
    opened: false
  }
  
  render () {
    const {
      props: {
        paragraph,
        title
      },
      state: {
        opened
      }
    } = this
    
    return (
      <div
        {...{
          className: `accordion-item, ${opened && 'accordion-item--opened'}`,
          onClick: () => { this.setState({ opened: !opened }) },
          id: "faq"
        }}
      >
        <div {...{ className: 'accordion-item__line' }}>
          <h5 {...{ className: 'accordion-item__title' }}>
            {title}
          </h5>
          <span {...{ className: 'accordion-item__icon' }}/>
        </div>
          <div {...{ className: 'accordion-item__inner' }}>
            <div {...{ className: 'accordion-item__content' }}>
              <div {...{ className: 'accordion-item__paragraph' }}>
                {paragraph}
              </div>
            </div>
          </div>
      </div>
    )
  }
}

const FAQ = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  ...props
}) => {

  const outerClasses = classNames(
    'section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'section-inner',
    'container',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  const data = [
    {
      title: 'How does NECOLE platform work?',
      paragraph: 'If you are new on the platform, you will be welcomed by a series of short contents to get you familiarised to NECOLE. You can expect a Netflix-like experience as you explore NECOLE- where you can browse learning content by channels and you can choose to follow the channels you like to give you personalised content feed on a daily basis.'
    },
    {
      title: 'When will I get my access to NECOLE?',
      paragraph: 'Once you have checked out your purchase, our team will verify the payment and we will send your personalised login credential to your registered email address within 24hours. Please make sure you entered the right email address during the registration.'
    },
    {
      title: 'Is there a monthly subscription option for NECOLE?',
      paragraph: 'Unfortunately, we do not provide monthly subscriptions at this moment. However, if in the future we decide to start monthly subscriptions, we will notify all subscribers accordingly. Terms and conditions will soon follow the notification.'
    },
    {
      title: 'Is NECOLE subscription refundable?',
      paragraph: 'NECOLE subscription is strictly not refundable for every successful payment made. NECOLE reserves the right to offer other forms of consideration at its sole discretion. Please note that this will be assessed on a case by case basis and giving such a consideration in one case does not create an obligation to do so in another.'
    },
    {
      title: 'Can I purchase more than 1 subscription?',
      paragraph: 'Each subscription is unique to one email address. The same email address would be used as the login ID to NECOLE. If you are purchasing for someone else, make sure you provide the correct email address during registration.'
    },
    {
      title: 'Can I cancel my NECOLE subscription?',
      paragraph: 'In the event you wish to cancel your NECOLE subscription, write in to us at learn@leaderonomics.com to inform us about the cancellation. We will deactivate your access and you would not be able to access NECOLE within 24hours from the notice of cancellation. Please be informed that NECOLE do not provide credit, refunds or prorated billing for subscriptions that are cancelled during the subscription period. However, NECOLE reserves the right to offer other forms of consideration at its sole discretion. Please note that this will be assessed on a case by case basis and giving such a consideration in one case does not create an obligation to do so in another.'
    },
  ];

  return (
    <section
      {...props}
      className={outerClasses}
    >
      <div className={innerClasses}>
        <h2 className="text-color-low">FAQ</h2>
        <div {...{ className: 'wrapper' }}>
          <ul {...{ className: 'accordion-list' }}>
            {data.map((data, key) => {              
              return (
                <li {...{ className: 'accordion-list__item', key }}>                  
                  <AccordionItem {...data} />
                </li>
              )
            })}
          </ul>
        </div>
    

        </div>
    </section>
  );
}

FAQ.propTypes = propTypes;
FAQ.defaultProps = defaultProps;

export default FAQ;