import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { SectionProps } from '../../utils/SectionProps';
import TextLoop from "react-text-loop";

const propTypes = {
  children: PropTypes.node,
  ...SectionProps.types
}

const defaultProps = {
  children: null,
  ...SectionProps.defaults
}

const FutureReady = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  ...props
}) => {

  const outerClasses = classNames(
    'section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );


  return (
    <section
      {...props}
      className={outerClasses}
    >      
      <div className="spacer-32" />
      <div className="futureready-banner spacer-64">
        <div className="container">
          <div className={innerClasses}>
            <h2 className="ta-c fw-600">Top future-ready skills <span className="fw-400">covered by our experts</span></h2>
            <h3 className="fw-400 ta-c">
              <TextLoop interval={1800}>
                <span><em>Complex Problem Solving</em></span>
                <span><em>Critical Thinking</em></span>
                <span><em>Creativity</em></span>
                <span><em>People Management</em></span>
                <span><em>Coordinating with Others</em></span>
                <span><em>Emotional Intelligence</em></span>
                <span><em>Judgement and Decision Making</em></span>
                <span><em>Service Orientation</em></span>
                <span><em>Negotiation</em></span>
                <span><em>Cognitive Flexibility</em></span>
              </TextLoop>
            </h3>
          </div>
          <div className="spacer-64" />
        </div>
      </div>
    </section>
  );
}

FutureReady.propTypes = propTypes;
FutureReady.defaultProps = defaultProps;

export default FutureReady;