import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { SectionProps } from '../../utils/SectionProps';

const axios = require('axios');

const propTypes = {
  children: PropTypes.node,
  ...SectionProps.types
}

const defaultProps = {
  children: null,
  ...SectionProps.defaults
}

const defaultPrice = 75;
const defaultPrevPrice = 100;
const defaultPricePerDay = 0.20;
const defaultCurrency = 'USD';
const roundToDecimal = (num1, decimalPlaces) => ((Math.round(num1 * 100) / 100).toFixed(2))

class CurrencySelect extends React.Component {
  constructor(props) {
    super(props);
    this.state = { ratesArray: [], 
                   selection: defaultCurrency, 
                   price: defaultPrice, 
                   prevPrice: defaultPrevPrice,
                   pricePerDay: roundToDecimal(defaultPricePerDay),
                   json: {"rates":{"CAD":1.3146735942,"HKD":7.7501885528,"ISK":135.7579820665,"PHP":48.5301265398,"DKK":6.2392524931,"HUF":293.1282996732,"CZK":21.8872035532,"GBP":0.7571021537,"RON":4.056230621,"SEK":8.6432581916,"IDR":14770.9963965474,"INR":74.8479007794,"BRL":5.4567166681,"RUB":73.3852342244,"HRK":6.3125785636,"JPY":105.3716584262,"THB":31.2402581078,"CHF":0.9059750272,"EUR":0.838012235,"MYR":4.172462918,"BGN":1.6389843292,"TRY":7.3564904048,"CNY":6.9112545043,"NOK":8.837258024,"NZD":1.505824185,"ZAR":17.194586441,"USD":1.0,"MXN":22.1092767954,"SGD":1.3641163161,"AUD":1.3766864996,"ILS":3.3998994385,"KRW":1178.2200620129,"PLN":3.6823933629},"base":"USD","date":"2020-08-19"}
                  };
    this.handleChange = this.handleChange.bind(this)    
  }

  componentDidMount(){

    // Get updated currency exchange here
    var url = 'https://api.exchangeratesapi.io/latest?base=' + defaultCurrency;
    var self = this;

    axios.get(url)
      .then(function (response) {        
        self.setState({ json: response.data });
      })
      .catch(function (error) {        
        console.error(error);
      })
      .then(function () {
        var arr = [];
        var ratesJson = self.state.json.rates;

        Object.keys(ratesJson).forEach(function (key) {
          arr.push({ key: key, value: key, rate: ratesJson[key] });
        })

        arr.sort((a, b) => {
          if (a.value > b.value)
            return 1;
          else
            return -1;
        });

        self.setState({ ratesArray: arr });
      });      
  }

  handleChange(event) {        
    var _price = defaultPrice * this.state.json.rates[event.target.value];        
    var _pricePerDay = roundToDecimal(_price / 365, 2);
    var _prevPrice = Math.round(defaultPrevPrice * this.state.json.rates[event.target.value]);            
    this.setState({ selection: event.target.value,
                    price: Math.round(_price),
                    prevPrice: _prevPrice,
                    pricePerDay: _pricePerDay
                     });
  }

  render(){    
    return(
      <>
        <del>${this.state.prevPrice}</del>
        <h2 className="invert-color text-color-low m-0">${this.state.price}/year</h2>
        <div>Just &lt;${this.state.pricePerDay} a day!</div>
        <div className="container-xs p-24 width-100">
          <select className="form-select center-content" id="color" onChange={this.handleChange} value={this.state.selection}>
            {this.state.ratesArray.map(element =>
              <option key={element.key}>{element.value}</option>
            )}
          </select>
        </div>
      </>
    );    
  }  
}

const Subscribe = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  ...props
}) => {

 
  const outerClasses = classNames(
    'section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );
  
  return (
    <section
      {...props}
      className={outerClasses}
      id="pricing"
    >
      <div className="container center-content">
        <div className={innerClasses}>
          <CurrencySelect />
          <div className="text-xxs">* The price shown is an estimation based on exchange rate</div>            
          <div className="spacer-24"></div>
          <div className="container-xs center-content text-xs ta-l invert-color text-color-low small-split-container">
              <div className="small-split-alt-left pr-8">✓</div>
              <div className="small-split-alt-right ta-l"><b>BONUS</b> access to live webinars every month, a value of USD100. Plus we are giving you access to all of our past recorded webinars from industry experts at no charge.</div>                
              </div>
          </div>
      </div>

    </section>
  );
}

Subscribe.propTypes = propTypes;
Subscribe.defaultProps = defaultProps;

export default Subscribe;